import Vue from 'vue'
import Router from 'vue-router'
import ShowMap from '@/components/ShowMap'
import Detail from '@/components/Detail'
import cs from '@/components/cs'
import chuancan from '@/components/chuancan'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ShowMap',
      component: ShowMap
    },
    {
      path: '/detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/cs',
      name: 'cs',
      component: cs
    },
    {
      path: '/chuancan',
      name: 'chuancan',
      component: chuancan
    },

  ]
})



