<template>
    <div>
      <!--<el-button @click="changeWeathershow" type="primary">加切换天气</el-button>{{departureMap1}}-->
    <div id="map">
      <div  style="position: absolute;z-index:999 ;margin-top: -10px ;margin-left: 16px ;font-size: 80px; font-weight: bolder; font-family: 'Times New Roman';">VoyOpt Training Simulator</div>
      <div id="weather" style="position: absolute;z-index:999 ;margin-top: 120px ;margin-left: 16px ;font-size: 30px">{{weather}}</div>
      <div id="time" style="position: absolute;z-index:999 ;margin-top: 120px ;margin-left: 16px ;font-size: 30px">{{pointTime}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        map: "",
        //先纬度lat，经度lon
        markPoint:[
          [51.28978971079651, -55.662503347656255],
          [53.540672094759096, -42.12734709765626],
          [54.05974810317075, -26.13125334765626],
          [52.90927216824344,-8.20156584765626],
        ],
        midlat:'42.09867778441744',
        midlon:'-32.52804289843751',
        suofang:'4',
        departureMap0:'',
        departureMap1:'',
        destinationMap0:'',
        destinationMap1:'',
        circleFirst:'',
        circleLast:'',
        sailingMapI:0,
        sailingMapPoints:[],
        polygon:'',
        pointMarks:[],
        pointMark:'',
        pointMarkSize:50000,
        iColor:0,
        markIndex:'',
        pathColor:['#08E734','#C75D17','#C7C717',],
        heading:[],
        wind:'',
        windurl:'',
        current:'',
        currenturl:'',
        waves:'',
        wavesurl:'',
        btnum:'',
        weather:'',
        times:'',
        pointTime:'',
    };
  },
  mounted() {
    this.initDate();
    //监听并读取浏览器储存的经纬度信息
    //出发地经纬度
    window.addEventListener('storage', event => {
        if(event.key === 'departurelon') {
          //精度信息
          this.departureMap1=event.newValue;
          // console.log('lon',this.departureMap1);
        }
      });
    window.addEventListener('storage', event => {
        if(event.key === 'departurelat') {
          //纬度信息
          this.departureMap0=event.newValue;
          // console.log('lat',this.departureMap0);
        }
      });
  //  目的地经纬度
    window.addEventListener('storage', event => {
        if(event.key === 'destinationlon') {
          //纬度信息
          this.destinationMap1=event.newValue;
        }
      });
    window.addEventListener('storage', event => {
        if(event.key === 'destinationlat') {
          //经度信息
          this.destinationMap0=event.newValue;
        }
      });
  //  监听航行经过点信息
    window.addEventListener('storage', event => {
        if(event.key === 'sailingMap') {
          //经度信息
          var Points=event.newValue;
          var pPoints=Points.split(',');
          var pp=[];
          this.times=[];
          this.heading=[];
          for (var i in pPoints){
            if(i%4 == 3 && i>0){
              pp.push([pPoints[i-3],pPoints[i-2]]);
              this.times.push(pPoints[i-1]);
              this.heading.push(pPoints[i]);
            }
          }
          this.sailingMapI=this.sailingMapI+1;
          // console.log(this.sailingMapI);
          this.sailingMapPoints=pp;
          // console.log(typeof (this.sailingMapPoints),'???');
          // console.log(this.sailingMapPoints);
        }
      });
  //  监听传入的实时地点pointIndex 下标
    window.addEventListener('storage', event => {
      if(event.key === 'pointIndex') {
        //接受下标
        this.markIndex=event.newValue;
        //根据下标实时改变修改天气图层url地址
        var windlist=localStorage.getItem('windurls').split(',');
        this.windurl=windlist[this.markIndex];
        var currentlist=localStorage.getItem('currenturls').split(',');
        this.currenturl=currentlist[this.markIndex];
        var waveslist=localStorage.getItem('wavesurls').split(',');
        this.wavesurl=waveslist[this.markIndex];

      }
    });
  //  监听天气按钮改变
     window.addEventListener('storage', event => {
      if(event.key === 'weathernum') {
        //天气按钮
        this.btnum=parseInt(event.newValue);
        console.log(this.btnum);
        console.log(typeof(this.btnum));

      }
    });
  },
  methods: {
    //初始化地图中心点位于Atlantic Ocean
    initDate() {
      if(this.map!=''){
        this.map.remove();
      }
      this.map = L.map("map", {
        center: [this.midlat,this.midlon], // 地图中心先纬度后精度
        zoom: this.suofang, //缩放比列
        // zoomSnap:4,
        zoomControl: false, //禁用 + - 按钮
        doubleClickZoom: false, // 禁用双击放大
        attributionControl: false // 移除右下角leaflet标识
      });
      let name = L.tileLayer(
        //zh-EN英文显示地图  zh-CN中文显示 http://mt0.google.cn/vt/lyrs=y@160000000&hl=zh-EN&gl=CN&src=app&y={y}&x={x}&z={z}&s=Ga
        'https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png',
      ).addTo(this.map);
      // console.log(this.map.options.zoom)
    },
    //画出发地绿点
    departureMark(){
      //出发点更改后，删除之前存在的出发点、目的地、路线图、实时点
      if (this.circleFirst!=''){
       this.circleFirst.remove();
       if(this.circleLast!=''){
         this.circleLast.remove();
       }
       if(this.pointMarks[0]!=''){
          for(var i in this.pointMarks){
          this.pointMarks[i].remove();
        }
       }
       if(this.pointMark!=''){
         this.pointMark.remove();
       }
      }
      //初始值以起点为中心
      this.midlat=this.departureMap0;
      this.midlon=this.departureMap1;
      this.suofang=4;
      // console.log(this.map);
      this.initDate();

      this.circleFirst = L.circle([this.departureMap0,this.departureMap1], {
          color: 'green',
          // fillColor: '##09F709',
          fillOpacity: 1,
          radius: this.pointMarkSize,
      }).addTo(this.map);

    },
    //画目的地红点
    destinationMark(){
      //删除之前存在的目的地点
      if (this.circleLast!=''){
       this.circleLast.remove();
      }
      //取出发地目的地中点重新绘图  重置
      // console.log(this.destinationMap1);
      // console.log(this.departureMap1);

      this.midlat=(parseInt(this.destinationMap0)+parseInt(this.departureMap0))/2;
      this.midlon=(parseInt(this.destinationMap1)+parseInt(this.departureMap1))/2;
      // console.log(this.midlon);
      // 根据距离缩放地图
      var path=Math.abs(parseInt(this.destinationMap0)-parseInt(this.departureMap0))+Math.abs(parseInt(this.destinationMap1)-parseInt(this.departureMap1));
      // console.log(this.departureMap0,this.departureMap1,this.destinationMap0,this.destinationMap1);
      // console.log(path,'path');
      if(path>200){
        this.suofang=3;
        this.pointMarkSize=80000;
        // console.log(this.suofang);
      }else if(path>80) {
        this.suofang=4;
        this.pointMarkSize=50000;
        // console.log(this.suofang);

      }else if(path>30){
        this.suofang=5;
        this.pointMarkSize=30000;
        // console.log(this.suofang);
      }else {
        this.suofang=6;
        this.pointMarkSize=10000;
        // console.log(this.suofang);
      }

      this.initDate();

      //重绘出发点、不要直接调用绘制出发点方法
      if (this.circleFirst!=''){
       this.circleFirst.remove();
       if(this.circleLast!=''){
         this.circleLast.remove();
       }
       if(this.pointMarks[0]!=''){
          for(var i in this.pointMarks){
          this.pointMarks[i].remove();
        }
       }
       if(this.pointMark!=''){
         this.pointMark.remove();
       }
      }
     this.circleFirst = L.circle([this.departureMap0,this.departureMap1], {
          color: 'green',
          // fillColor: '##09F709',
          fillOpacity: 1,
          radius: this.pointMarkSize,
      }).addTo(this.map);
      //绘制目的地
      this.circleLast = L.circle([this.destinationMap0,this.destinationMap1], {
          color: 'red',
          // fillColor: '##09F709',
          fillOpacity: 1,
          radius: this.pointMarkSize
      }).addTo(this.map);
      // console.log(this.map.options.zoom)
    },
    //画路径
    drawMarks(){
       //画出封闭图形,删除之前画的路线图
      if (this.pointMarks[0]!=''){
        for(var i in this.pointMarks){
          this.pointMarks[i].remove();
        }

      }
      // console.log('开始画线段');
      // console.log(this.sailingMapPoints.length);
      var aa=['ad','sdw','fdfdsa'];
      // for(var a in aa.length-1){
      //   console.log('a',a)
      // }
      for(var q in this.sailingMapPoints){
        this.iColor = q % 3 ;
        // console.log(this.iColor,'iColor',this.sailingMapPoints.length-1,'this.sailingMapPoints.length-1');
        if( q>0){
           this.pointMarks[q]=L.polyline([this.sailingMapPoints[q-1],this.sailingMapPoints[q]],{
            //内部不透明度 0透明，1不透明
            //   fillOpacity:1,
             weight:20,
              color: this.pathColor[this.iColor],
            // fillColor: '#EE9611',
           }).addTo(this.map);
        }
      //  重新绘制起点终点避免被覆盖 先删除出发地、目的地及当前标记点
        if (this.circleFirst!=''){
         this.circleFirst.remove();
         if(this.circleLast!=''){
           this.circleLast.remove();
         }
         if(this.pointMark!=''){
           this.pointMark.remove();
         }
        }
         //绘制出发地
        this.circleFirst = L.circle([this.departureMap0,this.departureMap1], {
            color: 'green',
            // fillColor: '##09F709',
            fillOpacity: 1,
            radius: this.pointMarkSize,
        }).addTo(this.map);
        //绘制目的地
      this.circleLast = L.circle([this.destinationMap0,this.destinationMap1], {
          color: 'red',
          // fillColor: '##09F709',
          fillOpacity: 1,
          radius: this.pointMarkSize,
      }).addTo(this.map);


      }
      console.log('画完了')

    },
    //实时改变的点
    drawMark(){
      //heading区分方向 每22.5改变一次
      var shipdir, shipurl;
      shipdir=parseInt(this.heading[this.markIndex]);
      console.log(this.heading[this.markIndex]);
      if(shipdir<0){
        shipdir=shipdir+360
      }
      console.log(parseInt(shipdir/22.5));
      // shipurl='../../static/ship'+Math.abs(parseInt(shipdir/22.5))+'.png';
      switch (Math.abs(parseInt(shipdir/22.5))) {
        case 0:
          shipurl='../../static/ship0.png';
          break;
        case 1:
          shipurl='../../static/ship1.png';
          break
        case 2:
          shipurl='../../static/ship2.png';
          break
        case 3:
          shipurl='../../static/ship3.png';
          break
        case 4:
          shipurl='../../static/ship4.png';
          break
        case 5:
          shipurl='../../static/ship5.png';
          break
        case 6:
          shipurl='../../static/ship6.png';
          break
        case 7:
          shipurl='../../static/ship7.png';
          break
        case 8:
          shipurl='../../static/ship8.png';
          break
        case 9:
          shipurl='../../static/ship9.png';
          break
        case 10:
          shipurl='../../static/ship10.png';
          break
        case 11:
          shipurl='../../static/ship11.png';
          break
        case 12:
          shipurl='../../static/ship12.png';
          break
        case 13:
          shipurl='../../static/ship13.png';
          break
        case 14:
          shipurl='../../static/ship14.png';
          break
        case 15:
          shipurl='../../static/ship15.png';
          break
        case 16:
          shipurl='../../static/ship16.png';
          break
      }
      console.log(shipurl);
      //标记地点船只大小iconSize
       var myIcon = L.icon({
            iconUrl: shipurl,
            iconSize: [90, 90],
        });
      // 删除前一个标记地点
      if(this.pointMark != ''){
         this.pointMark.remove();
      }
      this.pointMark=L.marker(this.sailingMapPoints[this.markIndex],{
        icon:myIcon,
      }).addTo(this.map);
    },
      //改变天气图层
    changeWeathershow(){
      if(this.btnum<4){
        this.btnum++
      }else {
        this.btnum=0
      }
      // 更改后执行画图层
      this.changeWeather();
    },
    changeWeather(){
      if(this.btnum==1){
        //关闭所有之前的天气图层
        if (this.wind!=''){
          this.wind.remove()
        }
        if (this.current!=''){
          this.current.remove()
        }
        if (this.waves!=''){
          this.waves.remove()
        }
        // console.log(this.wavesurl);
        //加入浪图层
        this.waves = L.tileLayer(
          this.wavesurl,
        ).addTo(this.map);
        // console.log('执行了更改浪')
      }else if(this.btnum==2){
        //关闭所有之前的天气图层
        if (this.wind!=''){
          this.wind.remove()
        }
        if (this.current!=''){
          this.current.remove()
        }
        if (this.waves!=''){
          this.waves.remove()
        }
        // console.log(this.currenturl);
        //加入水流图层
        this.current = L.tileLayer(
          this.currenturl,
        ).addTo(this.map);
        // console.log('执行了更改水流')
      }else if(this.btnum==3){
        //关闭所有之前的天气图层
        if (this.wind!=''){
          this.wind.remove()
        }
        if (this.current!=''){
          this.current.remove()
        }
        if (this.waves!=''){
          this.waves.remove()
        }
        // console.log(this.windurl);
        //加入风图层
        this.wind = L.tileLayer(
          this.windurl,
        ).addTo(this.map);
         // console.log('执行了更改天气')
      }else {
        //关闭所有之前的天气图层
        if (this.wind!=''){
          this.wind.remove()
        }
        if (this.current!=''){
          this.current.remove()
        }
        if (this.waves!=''){
          this.waves.remove()
        }
      }

    },

  },
  watch:{
    //监听出发点改变后画图出发点
    departureMap0:{
      handler(newVal) {
        this.departureMark();
        // console.log('已执行画图')
      },
      deep:true
    },
    //监听目的地改变后画图目的地点
    destinationMap0:{
      handler(newVal) {
        this.destinationMark();
      },
      deep:true
    },
    //监听航行经过点改变后画图点
    sailingMapI:{
      handler(newVal) {
        this.drawMarks();
      },
      deep:true
    },
  //  通过监听下标实时改变标记点
    markIndex:{
      handler(newVal) {
        this.pointTime=this.times[this.markIndex];
        this.drawMark();
        this.changeWeather();
      },
      deep:true
    },
  //天气按钮参数改变btnum
    btnum:{
      handler(newVal) {
        this.changeWeather();
        //天气显示
        if(this.btnum==1){
          this.weather='Waves';
          document.getElementById('time').style.marginTop='160px';
          document.getElementById('weather').style.display='block';
        }else if(this.btnum==2){
          this.weather='Current';
          document.getElementById('time').style.marginTop='160px';
          document.getElementById('weather').style.display='block';
        }else if(this.btnum==3){
          this.weather='Wind';
          document.getElementById('time').style.marginTop='160px';
          document.getElementById('weather').style.display='block';
        }else{
          this.weather=''
          document.getElementById('weather').style.display='none';
          document.getElementById('time').style.marginTop='120px';
        }
      },
      deep:true
    }
  },

};
</script>

<style scoped>
#map {
  width: 100%;
  height: calc(100vh);
  z-index: 1;
}
</style>



