        methods: {
       //   初始请求数据页面打开及请求
          getOption(){
            // 路线参数选择路径http://129.16.62.11:4200/api/index
            var url='http://129.16.62.11:4200/api/index';
            this.$http.get(url).then(function (res){
              //接口返回结果是str转对象object
              this.cs=eval("("+res.bodyText+")");
              // console.log(this.cs);
              //提取ships放入option1里提供选择
              for(var i in this.cs.ships){
                this.option1.push({value:i,label:this.cs.ships[i]})
              }
              //提取shipmodels放入option2
              for(var i in this.cs.shipmodels){
                this.option2.push({value:i,label:this.cs.shipmodels[i]})
              }
              //提取optimizemethods放入options3
              for(var i in this.cs.optimizemethods){
                this.option3.push({value:i,label:this.cs.optimizemethods[i]})
              }
              //提取region放入options4  出发地和目的地的大片海域
              for(var i in this.cs.locations){
                this.option4.push({value:i,label:this.cs.locations[i].region})

              }

              //提取departuretime放入options7
              for(var i in this.cs.departuretime){
                this.option7.push({value:i,label:this.cs.departuretime[i]})
              }
              // this.option1=this.cs.ships;


              // console.log(res.bodyText);

            },function (err) {
                 console.log(err)
            });
          //  刷新，或者首次打开默认清理浏览器保存的信息自定义的localStorage
            localStorage.setItem('sailingMap', '');
            localStorage.setItem('windurls', '');
            localStorage.setItem('currenturls', '');
            localStorage.setItem('wavesurls', '');
            localStorage.setItem('departurelon', '');
            localStorage.setItem('departurelat', '');
            localStorage.setItem('destinationlon',  '');
            localStorage.setItem('destinationlat',  '');
            localStorage.setItem('pointIndex',  '');
            localStorage.setItem('weathernum',  '0');

          },
       //  连接websocket硬件操作
          connectWebsocket(){
            if(typeof(WebSocket) === "undefined"){
                alert("您的浏览器不支持socket")
            }else{
                // 实例化socket 测试用端口192.168.0.58实际端口127.0.0.1
                this.socket = new WebSocket('ws://127.0.0.1:8181');
                // 监听socket连接
                this.socket.onopen = this.open;
                // 监听socket错误信息
                this.socket.onerror = this.error;
                // 监听socket消息
                this.socket.onmessage = this.getMessage
            }
          },
          open() {
              console.log("socket连接成功")
          },
          error() {
              console.log("连接错误")
          },
          //收到硬件消息后的处理
          //前三是AD、后四是按钮参数有效（一共11个）
          // 0,0,0,0,0,0,1,NoClick1,NoClick2,NoClick3,NoClick4
          // X,Y,Z,0,0,0,1,Click1,  Click2, Click3,   Click4
          getMessage(msg) {
              console.log(msg.data);
              var orders =msg.data.split(',');
              //3AD消息处理  3AD任意一个传0 不处理视图赋值
            if(orders[0]!=0 && orders[1]!=0 && orders[2]!=0){
              this.enginepower=orders[0];
              this.speed32 =orders[1];
              this.heading =orders[2];
            }


              //4按钮处理
              //同时按下plan、info  显示图片隐藏Options   Motion
              // info按钮  对应收到的第九个参数orders[8]
              // plan按钮  对应收到的第八个参数orders[7]
              if(orders[7]=='Click1' && orders[8]=='Click2'){
                document.getElementById('bgcimg').style.display='block';
                document.getElementById('Options').style.display='none';
                document.getElementById('Motion').style.display='none';
              }else if(orders[8]=='Click2'){
                //执行显示motion隐藏optimize
                document.getElementById('bgcimg').style.display='none';
                document.getElementById('Options').style.display='none';
                document.getElementById('Motion').style.display='block';
                this.drawEcharts();
              }else if(orders[7]=='Click1'){
                 //执行显示optimize隐藏motion
                document.getElementById('bgcimg').style.display='none';
                document.getElementById('Motion').style.display='none';
                document.getElementById('Options').style.display='block';
                this.drawEcharts();
              }

              //ship按钮  对应收到的第十个参数orders[9]
              if(orders[9]=='Click3'){
                if( this.resoptimize!= ''){
                  if(this.pointIndex<(this.resoptimize.length-1)){
                    this.pointIndex++
                  }else {
                    this.pointIndex=0
                  }
                }

              }
              //weather按钮     对应收到的第十一个参数orders[10]
              if(orders[10]=='Click4'){
                if(this.weathernum < 3 ){
                  this.weathernum++;
                  //  浏览器传递给map窗口页面
                  localStorage.setItem('weathernum', this.weathernum);
                }else {
                  // 大于3关闭天气图层 使this.weathernum归零
                  this.weathernum=0;
                  //  浏览器传递给map窗口页面
                  localStorage.setItem('weathernum', this.weathernum);
                }
              }


          },
          send(){
              this.socket.send(params)
          },
          close(){
              console.log("socket已经关闭")
          },


       // 画图到ID DIV上
          drawEcharts(){
            var myChart1 = this.$echarts.init(document.getElementById('echarts1'));
            myChart1.setOption(this.options1);
            // var myChart2 = this.$echarts.init(document.getElementById('echarts2'));
            // myChart2.setOption(this.options2);
            var myChart3 = this.$echarts.init(document.getElementById('echarts3'));
            myChart3.setOption(this.options3);
            // var myChart4 = this.$echarts.init(document.getElementById('echarts4'));
            // myChart4.setOption(this.options4);
            var myChart5 = this.$echarts.init(document.getElementById('echarts5'));
            myChart5.setOption(this.options5);
            var myChart6 = this.$echarts.init(document.getElementById('echarts6'));
            myChart6.setOption(this.options6);
          },

       //   提交Optimize请求
          postOptimize(){
            //不接受提交空数据
            if( this.departurelon!='' &&
                this.departurelat!='' &&
                this.destinationlon!='' &&
                this.destinationlat!=''&&
                // this.value1 !='' &&
                // this.value2 !='' &&
                this.value3!='' &&
                this.value7!=''

               ){
              this.showbt1='Calculating';
              //Optimize接口地址
              var apiStr = 'http://129.16.62.11:4200/api/optimize';
              // 拼接kv串
              // var OptimizeParams ='ship='+this.option1[this.value1].label+'&shipmo`de`l='+this.option2[this.value2].label+'&optimizemethod='+this.option3[this.value3].label+'&departuretime='+this.option7[this.value7].label+'&destinationtime='+'2015-01-01 00:00:00'+'&deplon='+this.departurelon+'&deplat='+this.departurelat+'&deslon='+this.destinationlon+'&deslat='+this.destinationlat;
              // console.log(OptimizeParams);
              //开始请求图表信息
              this.$http.post(apiStr,
              {
                // direction:'east',
                ship:'laurine1',
                shipmodel:"ShipEnergyPerformaceModel",
                optimizemethod:this.option3[this.value3].label,
                departuretime:this.option7[this.value7].label,
                destinationtime:'2015-01-01 00:00:00',
                // departurePosition:this.option5[this.value5].label,
                deplon:this.departurelon,
                deplat:this.departurelat,
                // destinationPosition:this.v6,
                deslon:this.destinationlon,
                deslat:this.destinationlat,

              },
              {emulateJSON: true,timeout:0,headers:{'Content-Type':'application/json'}}
              ).then(function(res){
                  this.resoptimize=res.body.results;
                  console.log(this.resoptimize);
                  // console.log(res);
                  //清空前一个请求的画图数据
                  this.options1.xAxis.data=[];
                  this.options1.series[0].data=[];
                  // this.options1.series[1].data=[];
                  this.options2.xAxis.data=[];
                  this.options2.series[0].data=[];
                  this.options2.series[1].data=[];
                  this.options3.xAxis.data=[];
                  this.options3.series[0].data=[];
                  this.options3.series[1].data=[];
                  this.options4.xAxis.data=[];
                  this.options4.series[0].data=[];
                  //清空上次航行点信息
                  this.sailingMap=[];
                  //计算ETAhours
                  this.eta=this.getTimeValue(this.resoptimize[0].time,this.resoptimize[this.resoptimize.length-1].time).toFixed(2);
                  //提取最后累计使用燃料值toFixed(2)保留两位
                  this.fuelconsumption=this.resoptimize[this.resoptimize.length-1]['cumlative fuel'].toFixed(2);
                  //提取数据放入中间页面图表中
                  //统一X轴时间数据
                  for(var i in this.resoptimize){
                    //4个X轴传入时间数据
                    this.options1.xAxis.data.push(this.resoptimize[i].time);
                    this.options2.xAxis.data.push(this.resoptimize[i].time);
                    this.options3.xAxis.data.push(this.resoptimize[i].time);
                    this.options4.xAxis.data.push(this.resoptimize[i].time);
                    //options1数据放入speed、speed through water  内涵空格的键用['xxx']  无需.xxx
                    this.options1.series[0].data.push(this.resoptimize[i].speed.toFixed(2));
                    // this.options1.series[1].data.push(this.resoptimize[i]['speed through water'].toFixed(2));
                    //options2数据放入effective power、sharftpower  内涵空格的键用['xxx']  无需.xxx
                    this.options2.series[0].data.push(this.resoptimize[i]['effective power'].toFixed(2));
                    this.options2.series[1].data.push(this.resoptimize[i].sharftpower.toFixed(2));
                    //options3数据放入fuel per hour、fuel per nm  内涵空格的键用['xxx']  无需.xxx
                    this.options3.series[0].data.push(this.resoptimize[i]['fuel per hour'].toFixed(2));
                    this.options3.series[1].data.push(this.resoptimize[i]['fuel per nm'].toFixed(2));
                    //options4数据放入resistance  内涵空格的键用['xxx']  无需.xxx
                    this.options4.series[0].data.push(this.resoptimize[i].resistance.toFixed(2));
                    //存入航行路线经过点经纬度sailingMap  时间  船方向
                    this.sailingMap.push([this.resoptimize[i].lat,this.resoptimize[i].lon,this.resoptimize[i].time,this.resoptimize[i].heading])

                  }
                  this.drawEcharts();

                  this.showbt1="Success!";
                  //把经过地点信息发送至map里浏览器储存
                  localStorage.setItem('sailingMap', this.sailingMap);
                  // console.log("响应结果res");
                  // console.log(res);
                  //  开始请求天气信息wind/current/waves
              this.windurls=[];
              this.currenturls=[];
              this.wavesurls=[];
              //  wind
              this.$http.get('http://129.16.62.11:4200/api/wind/index.json').then(function (res){
                for(var i in res.body){
                  var urls='http://129.16.62.11:4200/api'+res.body[i].url;
                  this.windurls.push(urls);
                }
                //  通过浏览器传给地图窗口
                localStorage.setItem('windurls', this.windurls);
                // console.log(this.windurls);
                // console.log('执行了天气请求');
              },function (err) {
                 console.log(err)
              });
              //  current
              this.$http.get('http://129.16.62.11:4200/api/current/index.json').then(function (res){
                for(var i in res.body){
                  var urls='http://129.16.62.11:4200/api'+res.body[i].url;
                  this.currenturls.push(urls);
                }
                //  通过浏览器传给地图窗口
                  localStorage.setItem('currenturls', this.currenturls);
              },function (err) {
                 console.log(err)
              });
              //  waves
              this.$http.get('http://129.16.62.11:4200/api/waves/index.json').then(function (res){
                for(var i in res.body){
                  var urls='http://129.16.62.11:4200/api'+res.body[i].url;
                  this.wavesurls.push(urls);
                }
                //  通过浏览器传给地图窗口
                localStorage.setItem('wavesurls', this.wavesurls);
                // console.log(this.wavesurls);
              },function (err) {
                 console.log(err)
              });
              });
            }else {
                 alert('Some params null !');
               }




          },
       //计算ETAhours
          getTimeValue(start,end) {
           var stime = new Date(start).getTime();
           var etime = new Date(end).getTime();
           var usedTime = etime - stime;  //两个时间戳相差的毫秒数
           var days = Math.floor(usedTime / (24 * 3600 * 1000));
           //计算出小时数
           var leave1 = usedTime % (24 * 3600 * 1000);    //计算天数后剩余的毫秒数
           var hours = Math.floor(leave1 / (3600 * 1000));
           //计算相差分钟数
           var leave2 = leave1 % (3600 * 1000);        //计算小时数后剩余的毫秒数
           var minutes = Math.floor(leave2 / (60 * 1000));
           var time = days*24+hours+minutes/60;
           // var time = days;
           return time
         },
       // 提交Motion计算
          postMotion(){
            //Motion接口地址
            var apiStr = 'http://129.16.62.11:4200/api/motion';
            //不接受空数据提交,存在0数据也不提交
            if(this.speed32!=''&&
               this.speed32!=0&&
               this.enginepower!=''&&
               this.enginepower!=0 &&
               this.heading!=''&&
               this.heading!=0 &&
               this.currents!=''&&
               this.currents!=0 &&
               this.currrentsdirection!=''&&
               this.currrentsdirection!=0 &&
               this.wind3!=''&&
               this.wind3!=0 &&
               this.wavedirection!=''&&
               this.wavedirection!=0 &&
               this.wavehs!=''&&
               this.wavehs!=0 &&
               this.waveperiod!=''&&
               this.waveperiod!=0 &&
               this.wavedirection!=''&&
               this.wavedirection!=0
            ){
              this.$http.post(apiStr,
              {
                speed:this.speed32,
                sharftpower:this.enginepower,
                heading:this.heading,
                current:this.currents,
                currentDirection:this.currrentsdirection,
                wind:this.wind3,
                windDirection:this.wavedirection,
                waveHS:this.wavehs,
                wavePeriod:this.waveperiod,
                waveDirection:this.wavedirection,

              },
              {emulateJSON: true,timeout:0,headers:{'Content-Type':'application/json'}}
              ).then(function(res){
                  //这个返回是json对象可以直接.属性用
                  this.resmotion=res.body;
                  //收到数据传给第三分页的上部数据
                  // add engine power
                  this.enginepower=this.resmotion.sharftpower;
                  this.speed31=this.resmotion.speed.toFixed(2);
                  this.fuelperhour3=this.resmotion.fuelperhour.toFixed(2);
                  this.maxpitch=Math.max(...this.resmotion.pitch).toFixed(2);
                  this.expectedeta=this.resmotion.expectedETA.toFixed(2);
                  this.speedreduction=this.resmotion.speedreduction;
                  this.resistance=this.resmotion.resistance.toFixed(2);
                  this.maxroll=Math.max(...this.resmotion.roll).toFixed(2);
                  //清空前表数据
                  this.options5.xAxis.data=[];
                  this.options6.xAxis.data=[];
                  this.options5.series[0].data=[];
                  this.options6.series[0].data=[];
                  //第三分页图表数据赋值
                  for(var i in this.resmotion.time){
                    //表5和6x轴数据传入
                    this.options5.xAxis.data.push(this.resmotion.time[i]);
                    this.options6.xAxis.data.push(this.resmotion.time[i]);
                  //  表5和6 pitch、roll 数据传入
                    this.options5.series[0].data.push(this.resmotion.pitch[i]);
                    this.options6.series[0].data.push(this.resmotion.roll[i]);

                  }
                  this.drawEcharts();
                  // console.log("响应结果res");
                  // console.log(res);
              });

            }else{
              alert('Some params null or zero !!!');
            }


          },
       //   隐藏motion页面


       } ,
        mounted() {
          this.getOption();
          this.drawEcharts();
          this.connectWebsocket()
        },
        watch:{
          //监听海域信息，发生改变后连带改变出发地及目的地
          value4:{
            handler(newVal) {
              //先清空后添加  出发地目的地相关数据清空
              this.option5=[];
              this.value5='';
              this.value6='';
              this.departurelon='';
              this.departurelat='';
              this.destinationlon='';
              this.destinationlat='';
              //选择海域后将，出发地数据放入option5  出发地、目的地有东西区别
              for(var i in this.cs.locations[this.value4].coordinate){
                this.option5.push({
                  value:i,
                  label:this.cs.locations[this.value4].coordinate[i].name ,
                  lon:this.cs.locations[this.value4].coordinate[i].lon,
                  lat:this.cs.locations[this.value4].coordinate[i].lat,
                  direction:this.cs.locations[this.value4].coordinate[i].direction})
              }
              //清空目的地选择，先选出发地才让选目的地
              this.option6=[]
            },
            deep:true
          },
          //监听目的地选择，并将与目的地同方位数据从出发地数据选项中剔除   出发地目的地的direction不能相同
          value5:{
            handler(newVal) {
              if (this.value5!=''){
                this.option6=[];
                //保证更改出发地、目的地信息清空后再选择
                this.value6='';
                //深拷贝
                var array6=JSON.parse(JSON.stringify(this.option5));
                // console.log(5,this.option5[this.value5]);
                // console.log(this.option5[this.value5].direction);
                for(var i in array6){
                  // console.log(i);
                  if( array6[i].direction != this.option5[this.value5].direction ){
                    // console.log(i,array6[i].direction);
                    this.option6.push(array6[i]);
                    // this.option6.splice(i,1)
                  }
                }
                // this.option6.splice(this.value5,1);
                // console.log(this.option6);
                //出发地经纬度赋值
                this.departurelon=this.option5[this.value5].lon;
                this.departurelat=this.option5[this.value5].lat;
                //将出发地经纬度存入浏览器
                 localStorage.setItem('departurelon', this.departurelon);
                 localStorage.setItem('departurelat', this.departurelat);
                //二次更改出发地如果和目的地相同，则清空目的地的信息
                if(this.value6){
                  if(this.option6[this.value6].direction == this.option5[this.value5].direction ){
                  this.value6='';
                  this.destinationlon='';
                  this.destinationlat='';
                  }
                }

              }
            },
            deep:true
          },
          //监听目的地选择
          value6:{
            handler(newVal) {
              //目的地地点名非空执行
              if(this.value6 != ''){
                for(var i in this.option6){
                if(this.value6==this.option6[i].value){
                  // console.log(i)
                  // console.log(this.option6[i].label)
                  this.v6=this.option6[i].label;
                  this.destinationlon=this.option6[i].lon;
                  this.destinationlat=this.option6[i].lat;
                  //将目的地经纬度存入浏览器
                 localStorage.setItem('destinationlon', this.destinationlon);
                 localStorage.setItem('destinationlat', this.destinationlat);

                  // alert(this.v6)
                }
              }
              }
            },
            deep:true
          },
          pointIndex:{
            handler(newVal) {
              //根据下标传lon和lat,下标保存至浏览器
                localStorage.setItem('pointIndex', this.pointIndex);
              // 按ship按钮,options页面传值
                this.wind=this.resoptimize[this.pointIndex].wind.toFixed(2);
                this.current=this.resoptimize[this.pointIndex].current.toFixed(2);
                this.waveheight=this.resoptimize[this.pointIndex].waveheight.toFixed(2);
                this.period=this.resoptimize[this.pointIndex].waveperiod.toFixed(2);
              // 按ship按钮,motion页面传值
                this.enginepower=this.resoptimize[this.pointIndex]['effective power'].toFixed(2);
                this.speed32=this.resoptimize[this.pointIndex].speed.toFixed(2);
                this.heading=this.resoptimize[this.pointIndex].heading.toFixed(2);
                this.currents=this.resoptimize[this.pointIndex].current.toFixed(2);
                this.currrentsdirection=this.resoptimize[this.pointIndex].currentangle.toFixed(2);
                this.wind3=this.resoptimize[this.pointIndex].wind.toFixed(2);
                this.winddirection=this.resoptimize[this.pointIndex].windangle.toFixed(2);
                this.wavehs=this.resoptimize[this.pointIndex].waveheight.toFixed(2);
                this.waveperiod=this.resoptimize[this.pointIndex].waveperiod.toFixed(2);
                this.wavedirection=this.resoptimize[this.pointIndex].waveangle.toFixed(2);
            },
            deep:true
          },
      }
    }
</script>

